import { graphql, Link, useStaticQuery } from 'gatsby';
import React from 'react';
import { useLocation } from '@reach/router';

export default function Layout({ children }: { children: React.ReactNode }) {
  const location = useLocation();
  const versions: Versions = useStaticQuery(graphql`
    query {
      allInfoYaml {
        nodes {
          version
          label
        }
      }
    }
  `);

  return (
    <div>
      <div className="flex flex-row space-x-2 px-2 py-3">
        {versions.allInfoYaml.nodes.map((item) => (
          <Link to={`${location.origin}/${item.version}`}>{item.label}</Link>
        ))}
      </div>
      <div className="px-2">{children}</div>
    </div>
  );
}

type Versions = {
  allInfoYaml: {
    nodes: { version: string; label: string }[];
  };
};
